import React from "react"
import { Helmet } from "react-helmet";

import {domain, hasHostName} from "../../url/urlChecker";

const baseUrl = domain;
const siteTitle = "TechWaifu";
const description = "Welcome to TechWaifu's blog site; a hub for tech, coding, ux, design, and more.";

const SEO = (data) => {
    const article = data.article;
    
    let image = `${baseUrl}/favicons/android-chrome-512x512.png`;
    if (data.image && !hasHostName(data.image)) {
        image = `${baseUrl}${data.image}`;
    } else if (data.image && hasHostName(data.image)) {
        image = data.image;
    }

    const seo = {
        title: data.title ? `${data.title}` : `${siteTitle}`,
        description: data.description ? data.description : description,
        image: image,
        author: data.author || "Mai Nguyen",
        url: data.urlPath ? `${baseUrl}${data.urlPath}` : `${baseUrl}`,
        keywords: data.keywords || [],
        metadata: data.metadata,
    };
    
    return (
        <Helmet title={seo.title} htmlAttributes={{ lang: "en", }}>
            <meta name="description" content={seo.description} />
            <meta name="apple-mobile-web-app-title" content="TechWaifu"></meta>
            <meta name="image" content={seo.image} />
            <meta name="author" content={seo.author}></meta>
            <meta name="robots" content="index, follow"></meta>
            <meta name="google-site-verification" content="NrgfHKi_g7EyePCHkVG5rjX_pG_wptAWaBPGgfZuYYg" />
            {seo.keywords.length > 0 &&
                <meta name="keywords" content={seo.keywords.reduce((cum, c) => {
                    if (cum === "") {
                        return c;
                    }
                    return `${cum},${c}`;
                }, "")} />
            }
            <meta property="og:site_name" content={siteTitle}></meta>
            {seo.url && <meta property="og:url" content={seo.url.endsWith("/") ? seo.url : `${seo.url}/`} />}
            {article && <meta property="og:type" content="article" />}
            {!article && <meta property="og:type" content="website" />}
            {seo.title && <meta property="og:title" content={seo.title} />}
            {seo.description && (
                <meta property="og:description" content={seo.description} />
            )}
            {seo.image && <meta property="og:image" content={seo.image} />}
            <meta name="twitter:card" content="summary_large_image" />
            {seo.title && <meta name="twitter:title" content={seo.title} />}
            {seo.description && (
                <meta name="twitter:description" content={seo.description} />
            )}
            <meta name="twitter:site" content={seo.url} />
            {seo.image && <meta name="twitter:image" content={seo.image} />}
            {(seo.metadata || []).map((metadata, idx) => {
                return (
                    <script type="application/ld+json" key={idx}>
                        {`${JSON.stringify(metadata)}`}
                    </script>

                );
            })}
        </Helmet>
    );
};

export default SEO
