import {AUTHOR_MAP} from "../author/authorInfo";

import {hasHostName} from "../url/urlChecker";

const description = "Welcome to TechWaifu's blog site; a hub for tech, coding, ux, design, and more.";

const offerMetadata = {
    "@type": "Offer",
    "availability": "http://schema.org/InStock",
};

const productMetadata =  {
    "@context": "https://www.schema.org",
    "@type": "product",
    "brand": "Acme",
    "logo": "https://techwaifu.com/favicons/android-chrome-512x512.png",
    "name": "WidgetPress",
    "image": "http://www.example.com/image.jpg",
    "description": "This is an excellent widget with 21 features and 4 colors.",
};

const publisherMetadata =  {
    "@type": "Organization",
    "name": "Mai Nguyen",
    "logo": {
        "@type": "ImageObject",
        "url": "https://techwaifu.com/favicons/android-chrome-512x512.png",
        "width": "192",
        "height": "192"
    }
}

const reviewContainer = {
    "@context": "https://schema.org",
    "@type": "Review",
  };

const review = {
    "@type": "Review",
    "inLanguage": "en",
    "publisher": publisherMetadata,
};

export const createReviewMetadata = ({ authorId, itemName, brandName, dateModified, datePublished, description, img, score }) => {
    const reviewMetadata = {
        ...review,
        "datePublished": datePublished,
        "dateModified": dateModified || datePublished,
        "reviewRating": {
            "@type": "Rating",
            "ratingValue": `${score}`,
            "bestRating": "100",
            "worstRating": "0"
        },
        "reviewBody": description,
        "author": {
            "@type": "Person",
            "name": AUTHOR_MAP[authorId || "mai"].name,
        },
    };

    const itemReview = {
        "@context": "https://www.schema.org",
        "@type": "product",
        "name": itemName,
        "image": hasHostName(img) ? img : `https://techwaifu.com${img}`,
        "brand": {
            "@type": "Brand",
            "name": brandName
        },
        "review": reviewMetadata,
    };

    return {
        ...reviewContainer,
        itemReviewed: itemReview,
        "author": {
            "@type": "Person",
            "name": AUTHOR_MAP[authorId || "mai"].name,
        },
    };
};

export const createProductMetadata = ({name, description, price, priceCurrency, image, brand, url }) => {
    const offers = { 
        ...offerMetadata,
        price,
        priceCurrency,
        url,
    };
    return {
        ...productMetadata,
        brand,
        description,
        name,
        image: hasHostName(image) ? image : `https://techwaifu.com${image}`,
        offers,
    }
};

export const createBlogPostingMetadata = ({authorId, coverPhoto, dateModified, datePublished, headline, mainEntityOfPage}) => {
    return {
        "@context": "https://schema.org",
        "@type": "BlogPosting",
        "mainEntityOfPage": mainEntityOfPage,
        "headline": headline,
        "author": {
            "@type": "Person",
            "name": AUTHOR_MAP[authorId || "mai"].name,
        },
        "datePublished": datePublished,
        "dateModified": dateModified || datePublished,
        "image": hasHostName(coverPhoto) ? coverPhoto : `https://techwaifu.com${coverPhoto}`,
        "publisher": publisherMetadata,
    };
}

export const blogMetadata = {
    "@context": "http://schema.org",
    "@type": "Blog",
    "name": "TechWaifu's Blog - A hub for tech, coding, ux, design, and more.",
    "url": "https://techwaifu.com",
    "description": description,
    "sameAs": [],
    "publisher": publisherMetadata,
};

export const createBlogPageMetadata  = (blogPosts) => {
    const blogs = blogPosts.map(({node}) => {
        return createBlogPostingMetadata({
            authorId: node.frontmatter.author || "mai",
            coverPhoto: node.frontmatter.cover_photo,
            datePublished: node.frontmatter.date,
            dateModified: node.frontmatter.modified_date,
            headline: node.frontmatter.title,
            mainEntityOfPage: `https://techwaifu.com/${node.frontmatter.slug}`,
        })
    });
    return {
        ...blogMetadata,
        blogPosts: blogs
    };
}