import React, { useEffect } from "react";
import ReactGA from 'react-ga';
import { MdClose } from 'react-icons/md';
import "./styles.scss";

export default function ImageDialog(props) {
    useEffect(() => {
        document.body.style.overflow = "hidden";
        ReactGA.event({
            category: 'ImageDialog',
            action: 'Appeared',
            label: 'ImageDialogOpenned',
            value: 1,
          });

        return () => {
            document.body.style.overflow = "";
        };
    });

    const closeDialog = () => {
        ReactGA.event({
            category: 'ImageDialog',
            action: 'Appeared',
            label: 'ImageDialogClosed',
            value: 1,
        });
        document.body.style.overflow = "";
        if (props.closeDialog) {
            props.closeDialog();
        }
    };
    const onKeyPressed = (e) => {
        if (e.keyCode === 27) {
            closeDialog();
        }
    };

    return (
        <div tabIndex="-1" className="ImageDialog" role="dialog" onKeyDown={onKeyPressed}>
            <button autoFocus className="CloseDialogButton" onClick={closeDialog} aria-label="Close dialog button"><MdClose /></button>
            <div className="ImageDialogCtr" role="presentation">
                <img src={props.img} alt={props.alt} />
            </div>
        </div>
    );
};