import React, { useEffect, useCallback, useState, useRef } from "react";
import ImageDialog from "./ImageDialog";

import { LazyLoadImage } from 'react-lazy-load-image-component';
import queryString from "query-string";
import "./styles.scss";

const XS_WIDTH = 320;
const S_WIDTH = 480;
const MED_WIDTH = 720;
const MAX_WIDTH = 960;
const compressedSizes = [
	XS_WIDTH,
	S_WIDTH,
	MED_WIDTH,
	MAX_WIDTH,
];

function createSrcSet(url, isResponsive, supportsWebp, srcSetOverride) {
    let urlWithoutParams = url;
    if (url.indexOf("?") >= 0) {
        urlWithoutParams = url.substring(0, url.indexOf("?"));
    }
    const hiResImg = urlWithoutParams;

    let srcsetUrl = null;
    let webpSrcsetUrl = null;
    const parts = hiResImg.split(".");
    let lowResImg = hiResImg;
    if (isResponsive) {
        if (srcSetOverride) {
            srcsetUrl = srcSetOverride;
        } else {
            srcsetUrl = compressedSizes.reduce((cum, size) => {
                const url = parts[0] + "-" + size + "." + parts[1];
                if (size === XS_WIDTH) {
                    lowResImg = url;
                }
                const srcSetUrl = `${url} ${size}w`;
                if (cum === "") {
                    return srcSetUrl;
                }

                return cum + ", " + srcSetUrl;
            }, "");
        }

        if (supportsWebp) {
            webpSrcsetUrl = srcsetUrl.split(".jpg").join(".webp");
        }
    }

    return {
        lowResImg: lowResImg,
        hiResImg: hiResImg,
        srcsetUrl,
        webpSrcsetUrl
    };
}

function generateClass(className, loaded) {
    let classes = "";
    if (className) {
        classes += " " + className;
    }
    classes += (" " + (!loaded ? "LazyLoadAnimation" : "")).trim();
    return classes;
}

const placeholderSrc = (img) => {
    let { width, height } = img;
    return `data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${width} ${height}"%3E%3C/svg%3E`;
};

const LazyLoadImageWithDimensions = (props) => {
    const ref = useRef(null);
    const { url, alt, isResponsive, srcSet: srcSetOverride, sizes, supportsWebp } = props.img;

    const {lowResImg, hiResImg, srcsetUrl, webpSrcsetUrl} = createSrcSet(url, isResponsive, supportsWebp, srcSetOverride);

    const [state, setState] = useState({
        isExpanded: false,
        loaded: false,
        src: placeholderSrc(props.img),
        srcSet: null, 
        webpSrcsetUrl: null,
        shouldFetch: true
    });

    const removeListeners = () => {
        window.removeEventListener("scroll", handleScroll);
    };

    const removeListenersCallback = useCallback(removeListeners);

    const loadImage = useCallback (() => {
        setState({
            ...state,
            src: lowResImg,
            shouldFetch: false,
            srcSet: srcsetUrl,
            webpSrcsetUrl: webpSrcsetUrl,
        });
        removeListenersCallback();
    }, [state, removeListenersCallback, lowResImg, srcsetUrl, webpSrcsetUrl]);

    const shouldLoadImage = useCallback((componentOffsetTop, userYPosition) => {
        return state.shouldFetch && !state.loaded && componentOffsetTop <= userYPosition + document.body.clientHeight + 100; 
    }, [state.loaded, state.shouldFetch]);

    const onImageLoad = () => {
        // const {lowResImg} = createSrcSet(url, isResponsive, supportsWebp, srcSet);
        if (!state.loaded && state.src === lowResImg) {
            setState({
                ...state,
                loaded: true
            });
        }
    };

    const onError = () => {
        setState({
            ...state,
            loaded: true
        });
        removeListeners();
    };

    const handleScroll = useCallback(() => {
        const yPosition = window.scrollY;
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        }
    }, [loadImage, shouldLoadImage]);

    useEffect(() => {
        const yPosition = window.scrollY;
        // Handles initial mount to check if image is already within threshold without scrolling.
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        } else {
            window.addEventListener('scroll', handleScroll);
        }

        return () => {
            removeListenersCallback();
        };
    }, [shouldLoadImage, loadImage, handleScroll, state, removeListenersCallback]);

    const onClick = () => {
        if (!state.isExpanded && props.isExpandable) {
            setState({
                ...state,
                isExpanded: true,
            });
        }
    };
    const onClose = () => {
        setState({
            ...state,
            isExpanded: false,
        });
    };

    const { isExpanded, loaded, src, srcSet, webpSrcsetUrl : webpSrcsetUrlState  } = state;
    const srcSetSizes = sizes ? sizes : "(max-width: 480px) 200px";
    return (
        <>
            <picture>
                {webpSrcsetUrlState &&
                    <source type="image/webp" srcSet={webpSrcsetUrlState} sizes={srcSetSizes} />
                }

                <img
                    ref={ref}
                    onLoad={onImageLoad}
                    onError={onError}
                    className={generateClass(props.className, loaded)}
                    src={src}
                    srcSet={srcSet}
                    sizes={isResponsive ? srcSetSizes : null}
                    alt={alt}
                    loading="lazy"
                />
            </picture>
            {isExpanded &&
                <ImageDialog img={hiResImg} alt={alt} closeDialog={onClose} />
            }

            <noscript>
                <img src={lowResImg} alt={alt} loading="lazy" />
            </noscript>
        </>
    );
}

const Img = (props) => {
    const { src } = props;
    const sepIdx = src.indexOf("?");

    if (sepIdx >= 0) {
        const params = queryString.parse(src.substring(sepIdx));
        if (params.height && params.width) {
            const img = {
                url: src,
                height: params.height,
                width: params.width,
                hiResUrl: params.hiRes,
                isResponsive: params.isResponsive,
                alt: props.alt,
                isNotExpandable: params.isNotExpandable,
                supportsWebp: params.supportsWebp,
                srcSet: params.srcSet,
                sizes: params.sizes,
            }
            return (
                <div className="LazyLoadImageWrapper">
                    <LazyLoadImageWithDimensions img={img} {...props} />
                </div>
            );
        }
    }

    return (
        <div className="LazyLoadImageWrapper">
            <LazyLoadImage {...props} effect="blur" threshold={300} />
        </div>
    );
};

export default Img; 